import React from "react";
import Link from "next/link";

import getMicroFrontend from "../src/get-micro-frontend";

type Props = {
    html?: string;
    scripts: string;
};

const SOURCE_URL = "http://localhost:3001/?component=header";
// const SOURCE_URL = "https://nationalmap.gov.au";
class Home extends React.Component<Props> {
    static async getInitialProps(ctx: any) {
        try {
            return {
                html: await getMicroFrontend(SOURCE_URL)
            };
        } catch (e) {
            console.error(e);

            return {
                html: "fail"
            };
        }
    }

    render() {
        return (
            <div>
                <div>
                    <button onClick={() => alert("hello")}>hello</button>
                    body 1
                    <Link href="/">
                        <a>Go to other page</a>
                    </Link>
                </div>
                <div
                    dangerouslySetInnerHTML={{
                        __html: this.props.html
                    }}
                />
            </div>
        );
    }
}

export default Home;
